import { Suspense } from 'react'
import { Points } from 'three';
/*
Summary
[In]points:
[Out]React
*/
export const  MapPoints = ({points}) => {
    if(points === null){
        points = new Points();
    }
    return (
        <Suspense fallback={null}>
          <primitive object={points} />
        </Suspense>
      )
}
